    title: "Menstrual Talk Webinar Organized",
    desc:
      "May was the International Period Month and May 28 is celebrated every year as Menstrual Hygiene Day. To mark the occasions this year in Nepal, National Health Corps (NHC) and Sky Is The Limit- Ashimit Udaan(SiTL) hosted a four-day webinar from May 25 to May 28 under Project Butterfly.\n\nThe webinar ,with its motto of ‘Menstrual Talk, Dignity First’, was organized to discuss and desigmatise menstruation, talk about formulating period-positive policies and share the context of Nepal and global views about the practice of Chhaupadi, according to a statement issued by NHC on Wednesday. The webinar featured Radha Paudel, founder of Global South Coalition for Dignified Menstruation, Ashwin Karki, an activist for dignified menstruation , Dr Basant Sharma, obstetrician and gynaecologist, Pashupati Kunwar, chairperson of Sambikas Nepal and Harihar Adhikari, youth astrologer and actor, along with other prominent experts in the field of menstrual dignity and health.\n\nThe webinar was conducted through Zoom and 205 people had registered to participate in it, according to Shreya Khanal, coordinator of Project Butterfly.The session were also streamed live on facebook where they gamered an average of 5000 views every day.\n\n “Our objective was to make people aware and start a conversation about periods and combat some of the taboos surrounding this very natural process,” said Khanal. This is what the webinar was designed around, bringing up topics of menstrual hygiene, dignity , male perspective on menstruation, common health and social issues surrounding menstrual cycles, etc.\n\nNHC and SiTL have always been active in the field of dignified menstruation and had initially planned to select and train various individuals from schools and colleges, enable them to be advocates for menstrual hygiene and dignity in their communities and organize panel discussions involving community members, elected representatives and authorities, Khanal informed, “However, the coronavirus epidemic forced us to cancel many of our plans and use digital media for our discussions.” she explained.\n\n The webinar was supported by Student’s Charitable Society, 2030 Youth Force Nepal, Untold Period Stories, Jagaran Nepal, Everest Academy, Ideas for Us, Chitwan Medical College and Medical Students for choice.",
